.loader {
    width: 50px;
    height: 50px;
    border: 5px solid transparent;
    border-top: 5px solid #3498db; /* синий цвет верхней линии */
    border-radius: 50%; /* круг */
    animation: spin 1s cubic-bezier(0.5, 0.2, 0.3, 0.8) infinite, colorShift 3s linear infinite;
}

/* Анимация вращения */
@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

/* Плавное изменение цвета */
@keyframes colorShift {
    0% {
        border-top-color: #3498db; /* голубой */
    }
    33% {
        border-top-color: #e74c3c; /* красный */
    }
    66% {
        border-top-color: #f1c40f; /* желтый */
    }
    100% {
        border-top-color: #3498db; /* обратно в голубой */
    }
}